@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* {
  box-sizing: border-box;
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

#root {
  height: 100vh;
}

#container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  /* flex-direction: column; */
  background-color: #f0f0f0;
}


.start_body {
  background: #4c56a1;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0; 
}

.logoImg {
  width: 130px;
  margin: 2rem auto;
}

.logo {
  text-align: center;
  display: block;
}

h1 {
  margin-top: 3rem;
  font-family: Pacifico;
  text-align: center;
  font-size: calc(3rem + 0.25vw + 0.5vh);
  color: rgba(255, 255, 255, 0.475);
}

.start_description {
  margin-top: 5rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 100;
  font-family: 'PT Sans', sans-serif;
  color: rgba(255, 255, 255, 0.8);
}

.start_btn {
  background-color: #4000c7;
  border-radius: 1.5rem;
  color: white;
  width: 15rem;
  height: 5rem;
  display: block;
  margin: 6rem auto;
  border: 0;
  outline: 0;
  font-family: Pacifico;
  font-weight: 800;
  cursor: pointer;
  transition: 0.1s;
  font-size: calc(1rem + 0.25vw + 0.5vh);
  animation: btnAni 1.5s ease-in-out infinite both;
}

.start_btn:hover {
  color: #4c56a1;
  background: rgba(255, 255, 255, 0.8);
}

@keyframes btnAni {
  from {
    transform: scale(1);
    transform-origin: center center;
    animation-timing-function: liner;
  }
  50% {
    transform: scale(1.04);
    animation-timing-function: liner;
  }
}
.end_body {
  background: #4c56a1;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0; 
  }

.ending_title {
  font-family: Pacifico;
  text-align: center;
  font-size: calc(3rem + 0.25vw + 0.5vh);
  color: rgba(255, 255, 255, 0.475);
  margin-top: 4rem;
}

.retry_button {
  background-color: #4000c7;
  border-radius: 1.5rem;
  color: white;
  width: 15rem;
  height: 5rem;
  display: block;
  margin-top: 7rem;
  margin-left: auto;
  margin-right: auto;
  border: 0;
  outline: 0;
  font-family: Pacifico;
  font-weight: 800;
  cursor: pointer;
  transition: 0.1s;
  font-size: calc(1rem + 0.25vw + 0.5vh);
  animation: btnAni 1.5s ease-in-out infinite both;
}

.retry_button:hover {
  color: #4c56a1;
  background: rgba(255, 255, 255, 0.8);
}
.nav {
  background-color: #4c56a1;
  color: white;
  /* display: flex; */
  align-items: center;
  flex: 0 1 200px;
  min-width: 200px;
}

.nav_title {
  font-size: 1rem;
  margin-bottom: 1rem;
  padding: 10px;
  text-align: center;
  font-weight: 500;
  background-color:#7b86d3;
}

ol {
  margin: 10px;
  -webkit-padding-start: 3ch;
          padding-inline-start: 3ch;
}

li {
  padding: 5px;
  border-bottom: 1px dotted #7b86d3;
}

li:hover {
  cursor: pointer;
}

.current_tab {
  color: rgb(255, 230, 0)
}
.chapter_modal {
    background-color: #fefefe;
    text-align: center;
    padding: 5px;
    width: 90%;
    font-family: 'PT Sans', sans-serif;
    color: #4000c7;
    margin: 0 auto;
    border-radius: 1rem;
}

.chapter_list {
    margin: 10px;
    border-bottom: 1px dotted #7b86d3;
    font-size: 14px;
}
.visual_container {
  flex: 0 0 auto;
  position: relative;
}

.visual_container > img {
  left: 30px;
  z-index: 10;
}

.visual_sub_title {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 0.8rem;
  transform: rotate(270deg);
  transform-origin: 0 0;
  color: #4000c7;
  position: absolute;
  bottom: 0;
  left: -21px;
  font-weight: 500;
}

.visual_third_title {
  border-bottom: 2px solid orange;
  display: inline-block;
  font-size: 0.8rem;
  letter-spacing: 1px;
  font-weight: 500;
  margin-left: 10px;
}

.remote, .local {
  position: relative;
  border-left: 28px solid #c5caec77;
  padding: 10px;
}

hr {
  border: none;
  border-top: 1px dotted #4000c7;
  margin: 0;
}

.remote {
  height: 250px;
  background-color: #c5caec34;
}

.local {
  height: 330px;
  background-color: #f0f0f0;
}

.area_box_container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.area_box {
  border: 1px solid #c5caec;
  background-color: #ffffff;
  width: calc(100% - 20px);
  flex: 1 0 auto;
  margin: 10px;
  border-radius: 1rem;
}

.project {
  height: 1px;
}

.staging {
  border: 1px solid #c5caec;
  height: 70px;
}

.untracked {
  border: 1px solid #c5caec;
  height: 70px;
}

.stash {
  border: 1px solid #c5caec;
  height: 150px;
}

.remote_ch3 {
  height: 150px;
  margin: 10px;
  border: 1px solid #4000c7;
  border-radius: 8px;
}

.origin_ch3 {
  border: 1px solid #c5caec;
  width: 98%;
  height: 220px;
  margin-left: 10px;
  margin-right: 10px;
}

.local_ch3 {
  border: 1px solid #4000c7;
  height: 400px;
  margin: 10px;
  border-radius: 8px;
}

.commit {
  height: 120px;
}

.svg {
  position: absolute;
}

.commit_ch3 {
  height: 13rem;
}

.layout {
  display: flex;
}

.left {
  flex: 0 1 auto;
  justify-content: center;
  align-items: center;
  width: 800px;
}

.right {
  flex: 0 1 auto;
  justify-content: center;
  align-items: center;
  width: 340px;
}

.file {
  color: crimson;
}

.github_logo {
  margin-left: 47%;
  margin-top: 200px;
  margin-bottom: 250px;
}

.svg {
  position: absolute;
}

svg {
  position: absolute;
}

.show_commit {
  animation: boxFade 1s linear alternate;
}

@keyframes boxFade {
  0% {
   opacity: 0;
  }
  100% {
   opacity: 1;
  }
 }

 .push_commit {
   margin-left: 90px;
   margin-top: 15px;
 }
.terminal {
  border-radius: 8px;
  background-color: #333;
  margin: 10px;
  overflow: hidden;
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 10px rgba(0,0,0,.5);
}

.bar {
  flex: 0 1 30px;
  font-size: 16px;
  font-weight: 700;
  background-color:#4c56a1;
  border-radius: 8px 8px 0 0;
  display: flex;
  align-items: center;
  color: white;
}

.circle {
  color: rgba(74, 74, 74, 0.913);
  background-color: rgb(147, 161, 173);
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 20px;
  text-align: center;
  align-items: center;
  margin-left: 8px;
}

.bar_description {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.fakeButton :nth-child(3) {
  background-color: rgb(109, 203, 104);
}

.fakeButton :nth-child(2) {
  background-color: rgb(233, 228, 78);
}

.fakeButton :nth-child(1) {
  background-color: rgb(233, 78, 78);
}

.question_description {
  color: #ccc;
  margin: 20px;
  flex: 0 1 50px;
}

.terminal_guide {
  color: white;
  font-family: 'Courier New', Courier, monospace;
  margin: 0 20px;
  overflow: auto;
  flex: 1 0 auto;
}

.input_line {
  flex: 0 1 30px;
  display: flex;
  margin: 20px;
  bottom: 1rem;
}

.input_line .input {
  color: white;
  font-family: 'Courier New', Courier, monospace;
  width: 100%;
  margin-right: 10px;
  flex: auto;
  height: 30px;
  font-size: 20px;
  border: none;
  background: transparent;
}

.hint_btn {
  border-radius: 40px;
  background-color: #4c56a1;
  margin: 0 5px;
  color: white;
  flex: 0 1 50px;
  font-size: 13px;
  line-height: 30px;
  text-align: center;
}

.hint_btn:hover {
  background-color: white;
  color: #4c56a1;
}

.enter_btn {
  flex: 0 1 100px;
  height: 30px;
  border-radius: 20px;
  border: 0;
}

.enter_btn:hover {
  background-color: #4c56a1;
  color: white;
}
.modal_backdrop {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.4);
  display: grid;
  place-items: center;
}

.modal_content {
  background-color: #fefefe;
  place-self: center;
  text-align: center;
  padding: 20px;
  border: 1px solid #888;
  width: 40%;
  font-family: 'PT Sans', sans-serif;
  color: #4000c7;
  border-radius: 1rem;
  box-shadow: 0 0 30px rgba(0,0,0,0.3);
}

.modal_description {
  font-size: 1.5rem;
  text-align: center;
  padding-top: 5rem;
}

.modal_btn {
  background-color: #4000c7;
  border-radius: 1.5rem;
  color: white;
  width: 10rem;
  height: 3rem;
  display: block;
  margin-top: 3rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 3rem;
  border: 0;
  outline: 0;
  font-family: Pacifico;
  font-weight: 800;
  cursor: pointer;
  transition: 0.1s;
  font-size: calc(1rem + 0.25vw + 0.5vh);
  animation: btnAni 1.5s ease-in-out infinite both;
}

.modal_btn:hover {
  color: #4c56a1;
  background: #d2bdff;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.hint_modal {
    position: absolute;
    background-color: #c5caec;
    bottom: 10%;
    right: 2%;
    height: 40px;
    border-radius: 10px;
    padding: 0px 10px;
    color: #333;
    font-size: 13px;
    box-shadow: bisque;
    text-align: center;
    display: flex;
    align-items: center;
    float: right;
}

